.header{
    padding: 2rem 1rem;
    background-color: rgba(42,245,154,1);
    background: rgba(42,245,154,1);
    background: linear-gradient(45deg, rgba(42,245,154,1) 0%, rgba(0,156,253,1) 100%);
    box-shadow: $shadow;
    border-bottom-left-radius: $border-radius;
    border-bottom-right-radius: $border-radius;
    text-align: center;
}

.profile-image{
    max-width: 140px;
    box-shadow: $shadow-sm;
    border-radius: 50%;
}