/*------------------------------------*\
#NAV
\*------------------------------------*/
.nav{
    width: 100%;
    display: flex;
    font-size: 1.2rem;
    padding: 1rem 0;
    position: fixed;
    bottom: 0;
    left: 0;
    box-shadow: $shadow;
    background-color: $white;
    &__link{
        flex-grow: 1;
        text-align: center;
        &__text{
            color: $grey-font;
            display: none;
        }
    }
}